import { MdDialogRef } from '@angular/material';
import { Component } from '@angular/core';

@Component({
  selector: 'app-alert-dialog',
  styleUrls : ['alert-dialog.component.scss'],
  template: `      
  <md-card-title>{{ title }}</md-card-title>
  <md-card-subtitle>{{ message }}</md-card-subtitle>
  <button type="button" md-raised-button md-button-sm color="primary" (click)="dialogRef.close(false)">OK</button>
  `
})
export class AlertDialogComponent {

  public title: string;
  public message: string;

  constructor(public dialogRef: MdDialogRef<AlertDialogComponent>) {

  }
}



