import { Injectable } from '@angular/core';

export interface BadgeItem {
  type: string;
  value: string;
}

export interface ChildrenItems {
  state: string;
  name: string;
  type?: string;
}

export interface Menu {
  state: string;
  name: string;
  type: string;
  icon: string;
  badge?: BadgeItem[];
  children?: ChildrenItems[];
}

const MENUITEMS = [
  {
    state: 'groups',
    name: 'Groups',
    type: 'link',
    icon: 'dvr'
  },
  {
    state: 'faces',
    name: 'Faces',
    type: 'link',
    icon: 'face'
  },
  {
    state: 'learn',
    name: 'Learn',
    type: 'link',
    icon: 'remove_red_eye'
  },
  {
    state: 'recognize',
    name: 'Recognize',
    type: 'link',
    icon: 'fingerprint'
  }
];

@Injectable()
export class MenuItems {
  getAll(): Menu[] {
    return MENUITEMS;
  }

  add(menu: Menu) {
    MENUITEMS.push(menu);
  }
}



