import { MdDialogRef } from '@angular/material';
import { Component } from '@angular/core';

@Component({
  selector: 'confirm-dialog',
  styleUrls : ['confirm-dialog.component.scss'],
  template: `
<md-card>
  <md-card-title>{{ title }}</md-card-title>
  <md-card-subtitle>{{ message }}</md-card-subtitle>
  <md-card-actions>
    <button type="button" md-raised-button md-button-sm color="warn" (click)="dialogRef.close(true)">OK</button>
    <button type="button" md-raised-button md-button-sm color="primary" (click)="dialogRef.close(false)">Cancel</button>
  </md-card-actions>

</md-card>
`
})
export class ConfirmDialog {

  public title: string;
  public message: string;

  constructor(public dialogRef: MdDialogRef<ConfirmDialog>) {

  }
}



