import { MdDialogRef } from '@angular/material';
import {AfterViewInit, Component, ViewChild} from '@angular/core';

@Component({
  selector: 'app-alert-dialog',
  styleUrls : ['alert-dialog.component.scss'],
  template: `      
      <!--<img src="{{src}}" #image style="height: 300px; width: 300px" >-->
      <canvas #canvas width="1000" height="1000" ></canvas>
  `
})
export class ImageDialogComponent implements AfterViewInit {
  @ViewChild('canvas') canvas;
  @ViewChild('image') image;
  public src: string;
  public x: string;
  public y: string;
  public h: string;
  public w: string;

  constructor(public dialogRef: MdDialogRef<ImageDialogComponent>) {

  }
  ngAfterViewInit(): void {
      const canvas = this.canvas.nativeElement;
      const context = canvas.getContext('2d');
      const source = new Image();
      source.onload = () => {
          context.drawImage(source, 0, 0);
          context.beginPath();
          context.rect(this.x, this.y, this.w, this.h);
          context.strokeStyle = '#FFFFFF';
          context.lineWidth = 5;
          context.stroke();
      };

      source.src = this.src;
  }
}



