import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule, Http } from '@angular/http';
import { LocationStrategy, HashLocationStrategy } from '@angular/common';

import { TranslateModule, TranslateLoader, TranslateStaticLoader } from 'ng2-translate/ng2-translate';
import { MaterialModule } from '@angular/material';
import { FlexLayoutModule } from '@angular/flex-layout';

import { JazzDialogComponent } from './material/dialog/dialog.component';

import { AppRoutes } from './app.routing';
import { AuthGuard } from './shared/guards/index';
import { AppComponent } from './app.component';
import { AdminLayoutComponent } from './layouts/admin/admin-layout.component';
import { AuthLayoutComponent } from './layouts/auth/auth-layout.component';
import { SharedModule } from './shared/shared.module';
import { AgmCoreModule} from 'angular2-google-maps/core';

import {ConfirmDialog} from './shared/dialog-service/confirm-dialog.component';
import {AlertDialogComponent} from './shared/dialog-service/alert-dialog.component';
import {EllipsisPipe} from './shared/utils/ellipsis';
import {ImageDialogComponent} from './shared/dialog-service/image-dialog.component';

export function createTranslateLoader(http: Http) {
  return new TranslateStaticLoader(http, './assets/i18n', '.json');
}

@NgModule({
  declarations: [
    EllipsisPipe,
    AppComponent,
    AdminLayoutComponent,
    AuthLayoutComponent,
    JazzDialogComponent,
    ConfirmDialog,
    AlertDialogComponent,
    ImageDialogComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    SharedModule,
    RouterModule.forRoot(AppRoutes),
    FormsModule,
    HttpModule,
    TranslateModule.forRoot({
      provide: TranslateLoader,
      useFactory: (createTranslateLoader),
      deps: [Http]
    }),
    MaterialModule,
    FlexLayoutModule,
    AgmCoreModule.forRoot({apiKey: 'AIzaSyA3IE4PX5fb3aUbHP1THMxx_XX9w1EDGJ0'})
  ],
  exports: [
      ConfirmDialog,
      AlertDialogComponent,
      ImageDialogComponent
  ],
  providers: [ AuthGuard , { provide: LocationStrategy, useClass: HashLocationStrategy }],
  entryComponents: [JazzDialogComponent, ConfirmDialog, AlertDialogComponent, ImageDialogComponent],
  bootstrap: [AppComponent],
})
export class AppModule { }


